<template>
    <div class="inmueble-card">
      <img :src="inmueble.imagen || getDefaultImage(inmueble.tipoInmueble)" :alt="inmueble.direccion" class="inmueble-image">
      <div class="inmueble-details">
        <h3 class="inmueble-price">{{ formatPrice(inmueble.precio) }}</h3>
        <p class="inmueble-address">{{ inmueble.direccion }}</p>
        <div class="inmueble-features">
          <span><i class="pi pi-home"></i> {{ inmueble.disponible ? 'Disponible' : 'No disponible' }} </span>
          <span><i class="pi pi-verified"></i> {{ inmueble.verificado ? 'Verificado' : 'No verificado' }} </span>
          <span><i class="pi pi-stop"></i> {{ inmueble.metrosCuadrados}} m2</span>
        </div>
      </div>
    </div>
  </template>
  
  <script>
  import defaultCasaImage from '@/assets/default-house.jpg';
  import defaultDepartamentoImage from '@/assets/default-departamento.jpg';
  import defaultTerrenoImage from '@/assets/default-terreno.jpg';
  
  export default {
    name: 'InmuebleCard',
    props: {
      inmueble: {
        type: Object,
        required: true
      }
    },
    methods: {
      formatPrice(price) {
        return new Intl.NumberFormat('es-CL', { style: 'currency', currency: 'CLP' }).format(price);
      },
      getDefaultImage(tipoInmueble) {
        switch (tipoInmueble) {
          case 'CASA':
            return defaultCasaImage;
          case 'DEPARTAMENTO':
            return defaultDepartamentoImage;
          case 'TERRENO':
            return defaultTerrenoImage;
          default:
            return defaultCasaImage; 
        }
      }
    }
  }
  </script>
  
  <style scoped>
  .inmueble-card {
    border: 1px solid #f5f5f5;
    border-radius: 8px;
    overflow: hidden;
    transition: box-shadow 0.3s ease;
  }
  
  .inmueble-card:hover {
    box-shadow: 0 4px 8px rgba(0,1,1,1.1);
  }
  
  .inmueble-image {
    width: 100%;
    height: 200px;
    object-fit: cover;
  }
  
  .inmueble-details {
    padding: 15px;
  }
  
  .inmueble-price {
    font-size: 1.4em;
    font-weight: bold;
    color: #07e90f;
    margin: 0 0 10px 0;
  }
  
  .inmueble-address {
    font-size: 1em;
    color: #f5f5f5;
    margin: 0 0 10px 0;
  }
  
  .inmueble-features {
    display: flex;
